            width: 60%;
            color: #f9f9f9;
            background-color: rgba(0, 0, 0, 0);
            border: 1px solid white;
            border-radius: 50px;
            -webkit-transition-duration: 1s;
                 -o-transition-duration: 1s;
                    transition-duration: 1s;
        }

            .name-container button:hover {
                cursor: pointer;
                color: #f49b42;
                border: 1px solid #f49b42;
            }


        .name-container span {
            color: white;
        }

    .card-container {
        background-color: #e8e8e8;
