        background-color: #DCDCDC;
        padding-top: 5%;
    }

        .tech {
            width: 15%;
            padding-bottom: 15%;
        }

    .project-details button {
        margin-left: 5%;
        width: 60%;
        margin-bottom: 4%;
        background-color: rgba(0, 0, 0, 0);
        border: 1px solid black;
        border-radius: 50px;
        -webkit-transition-duration: 1s;
             -o-transition-duration: 1s;
                transition-duration: 1s;
    }

        .project-details button:hover {
            color: #fff;
            border: 1px solid #f49b42;
            background-color: #f49b42;
        }

    #contact {
        background-color: #DCDCDC;
        padding-top: 15%;
        padding-bottom: 15%;
    }

    #contact * {
        display: block;
    }

    #contact-content input {
        margin-left: 30%;
        margin-bottom: 3%;
    }

    #contact-content textarea {
        margin-left: 20%;
        resize: none;
        width: 60%;
        margin-bottom: 5%;
    }

    #contact button {
        display: block;
        margin-left: 20%;
        background-color: rgba(0, 0, 0, 0);
        border: 1px solid black;
        border-radius: 50px;
        width: 60%;
        -webkit-transition-duration: 1s;
             -o-transition-duration: 1s;
                transition-duration: 1s;
    }

        #contact button:hover {
            border-color: gray;
            color: gray;
            cursor: pointer;
        }

    footer {
        background-color: #353535;
    }

    footer p {
        color: white;
    }

        footer img {
            width: 10%;
            margin-right: 10%;
        }
}

